'use client'

import Link from "next/link"
import { Button } from "@/components/ui/button"
import Hero from "@/components/about/hero"
import OurStory from "@/components/about/story"
import Mission from "@/components/about/mission"

const siteName = process.env.NEXT_PUBLIC_SITE_NAME || "Stampi"

export default function AboutPage() {
    return (
        <main className="bg-white">

            {/* ---------- HERO ---------- */}
            <Hero />

            {/* ---------- STORY ---------- */}
            <OurStory />

            {/* ---------- MISSION & VALUES ---------- */}
            <Mission/>

            {/* ---------- WHO IT’S FOR ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-4xl mx-auto px-6 text-center">
                    <h2 className="text-3xl font-semibold mb-6">
                        Who Uses <span className='logo-font'>{process.env.NEXT_PUBLIC_SITE_NAME ?? "site_name"}</span>?
                    </h2>
                    <p className="text-gray-600 text-lg leading-relaxed">
                        From cafés and salons to retail stores and service providers,
                        {siteName} is trusted by businesses that care about building lasting
                        customer relationships.
                    </p>
                </div>
            </section>

            {/* ---------- CTA ---------- */}
            <section className="py-20">
                <div className="max-w-4xl mx-auto px-6">
                    <div className="bg-primary rounded-2xl text-white text-center py-14 px-6">
                        <h2 className="text-3xl font-semibold mb-4">
                            Start Building Customer Loyalty
                        </h2>
                        <p className="mb-8 text-white/90">
                            Join shopkeepers using {siteName} to grow repeat customers.
                        </p>
                        <Button asChild size="lg" className="bg-white text-primary hover:bg-light-shade">
                            <Link href="/signup">Start Free Trial</Link>
                        </Button>
                    </div>
                </div>
            </section>

        </main>
    )
}
